import React, { Component } from "react";
import "./Section6.css";
import { Button } from 'react-bootstrap'

class Section6 extends Component {
  render() {
    return (
      <div className="container-fluid  ">
        <div className="row section6 ">
          <div className="col-lg-12 section6-div">
            <div className="bg-text">
              <h1>Stop wasting time being in the dark.</h1>
              <p>
                Pickle gives you the data and insights to save money and find
                opportunities.
              </p>
              <Button  href="/contactUs" className="book-button section6-btn demo-btn"  > Get a Demo </Button>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Section6;
