import React, { Component } from "react";
import './Form.css'

class Form extends Component {
    state = { companyName: 'Microsoft' };
 
	render() {
  	return (
    	<form>
      <span className="formtext">&#x3C;Form /&#x3E;</span>
    	  <input 
          type="text" 
          value={this.state.companyName}
          placeholder="Enter Company Name" 
          required 
        />
        <button>Go!</button>
    	</form>
    );
  }
}

export default Form;
