import React, { Component } from "react";
import "./Navbar.css";
import $ from "jquery"
import {
  Nav,
  Navbar,
  Button,
  Form,
} from "react-bootstrap";

class navbar extends Component {
  state ={className:"" };
  
  componentDidMount(){
    window.addEventListener("scroll", this.handleScroll);
    $(window).scroll(function() {
      if ($(this).scrollTop() > 1) {
        $(".navLink").addClass("default");
      } else {
        $(".navLink").removeClass("default");
      }
    });
    window.addEventListener("scroll", this.handleScroll);
    $(window).scroll(function() {
      if ($(this).scrollTop() > 1) {
        $(".navBtn").addClass("purple-btn");
      } else {
        $(".navBtn").removeClass("purple-btn");
      }
    });
    window.addEventListener("scroll", this.handleScroll);
    $(window).scroll(function() {
      if ($(this).scrollTop() > 1) {
        $(".nav-logo").addClass("purple-logo");
      } else {
        $(".nav-logo").removeClass("purple-logo");
      }
    });
   
  }
  handleScroll=()=>{
    if (window.pageYOffset > 0) {
        if(!this.state.className){
          this.setState({ className: "white" });
        }
    }else{
        if(this.state.className){
          this.setState({ className: "" });
        }
    }
   
  }

  

  render() {
    return (
      <header ref={(r)=>this.ref=r} className={this.state.className} >
        <div >
      <Navbar expand="lg" bg="" className="navbar-top" >
          <Navbar.Brand className="nav-logo purple-logo white-logo" bg-light href="/" style={{ marginLeft: "10%",width:"150px" }}></Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav"  />
          <Navbar.Collapse id="basic-navbar-nav">
              
            <Nav className=" nav-link" >
              <Nav.Link className="navLink white" ><div className="dropdown">Solutions <div className="dropdown-content">
                <li>
                <a href="#"><b>Sales Management</b> <br />Conversation Intelligence to manage and analyze your sales team.</a>
                <a href="#"><b>Sales Management </b> <br />Conversation Intelligence to manage and analyze your sales team.</a>
                <a href="#"><b>Sales Management </b> <br />Conversation Intelligence to manage and analyze your sales team.</a>
                </li>
              </div></div></Nav.Link>
              <Nav.Link className="navLink white" ><div className="dropdown">Product <div className="dropdown-content">
                <li>
                <a href="#"><b>Overview</b> <br />Know what happens on all your communication channels</a>
                <a href="#"><b>Transcription</b>  <br />Instant, reliable and accurate transcription</a>
                <a href="#"><b>Integrations </b> <br />View the possible integrations</a>
                </li>
              </div></div></Nav.Link>
              {/* <Nav.Link className="navLink white" >Product</Nav.Link> */}
              <Nav.Link className="navLink white" href="/Tasks" >Pricing</Nav.Link>
              <Nav.Link className="navLink white"  href="/Tasks" >Blog</Nav.Link>
            </Nav>
            
                
          </Navbar.Collapse>
          <Button  href="/contactUs" className="book-button navBtn purple" style={{marginLeft:"5%"}} > Book a Demo </Button>
        </Navbar>

      </div>
      </header>
      
      
    );
  }
}

export default navbar;
