import React, { Component } from "react"
import Navbar from '../FirstPage/navbar/Navbar'
import Footer from '../FirstPage/footer/Footer'
import Form from './Form';
import './ContactUs.css';

class ContactUs extends Component {
    render(){
        return(
            <div>
                <Navbar />
                <div className="contactus-bg"><h1 className="contactus">Contact Us</h1>
                
                <img className="contact-bg-img" src="https://assets-global.website-files.com/5db1f399eb19793b28a7982e/5e7cfa4fb7225a2c04dbcc13_5e7cf9bfee609846d1cdb917_Waves.svg"/>
                </div>
                <div className="contactus-form">
                    <Form />
                    
                </div>
                <Footer />
            </div>
        )
    }
};
export default ContactUs;