import React, { Component } from "react";
import "./Section4.css";
import { Card, CardDeck } from "react-bootstrap";

class Saction4 extends Component {
  // componentDidMount() {
  //     new WOW.WOW().init();
  //   }
  render() {
    return (
      <div className="card-section">
        <CardDeck>
          <Card className="card-section1">
            <Card.Img style={{width:"40%", paddingLeft:"30px"}}
              variant="top"
              src="https://assets-global.website-files.com/5db1f399eb19793b28a7982e/5e98b14a5f51225f0cac10f1_sales-icon.svg"
            />
            <Card.Body>
              <Card.Title style={{color:"white", paddingLeft: "20px" }}>SALES</Card.Title>
              <Card.Text style={{color:"white", paddingLeft: "20px" }}>
                Maximize your team’s performance by refining your sales
                pipeline. Understand what makes your A-players great at what
                they do. Find out ways to connect with your customers, improve
                your sales ROI, and raise the bar across the team!
              </Card.Text>
            </Card.Body>
          </Card>
          <Card className="card-section2">
            <Card.Img style={{width:"40%", paddingLeft:"30px" }}
              variant="top"
              src="https://assets-global.website-files.com/5db1f399eb19793b28a7982e/5e98b149edd80983b27db796_recordings-icon.svg"
            />
            <Card.Body>
              <Card.Title style={{color:"white", paddingLeft: "20px" }}>AUDIO RECORDINGS</Card.Title>
              <Card.Text style={{color:"white", paddingLeft: "20px" }}>
                Capture and recreate your best sales pitches. Improve retention
                and conversion rates across the board. Plus, bridge the gap
                between your business and its customers with the ultimate
                conversational AI.
              </Card.Text>
            </Card.Body>
          </Card>
          <Card className="card-section3">
            <Card.Img style={{width:"40%", paddingLeft:"30px"}}
              variant="top"
              src="https://assets-global.website-files.com/5db1f399eb19793b28a7982e/5e98b14aa264bdc50ff925ee_transaction-icon.svg"
            />
            <Card.Body>
              <Card.Title style={{color:"white", paddingLeft: "20px" }}>TRANSCRIPTIONS</Card.Title>
              <Card.Text style={{color:"white", paddingLeft: "20px" }} >
                Every word holds a world of value for your business. Never again
                miss on opportunities to grow and expand with the help of
                Pickle: the smart AI that transcribes all audio files. Instant,
                reliable and accurate transcription for your sales calls,
                podcasts, Zoom meetings and more!
              </Card.Text>
            </Card.Body>
          </Card>
        </CardDeck>
      </div>
    );
  }
}
export default Saction4;
